@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  @font-face {
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url('./assets/fonts/Poppins/Poppins-Black.ttf') format('ttf');
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Poppins',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Custom CSS */
/* For Webkit/Blink browsers */
::-webkit-scrollbar {
  width: 6px;
}

/* For Mozilla Firefox */
::-moz-scrollbar {
  width: 6px;
}

/* For Internet Explorer */
::-ms-scrollbar {
  width: 6px;
}

/* For Webkit/Blink browsers */
::-webkit-scrollbar-thumb {
  background-color: #2f2f2f;
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #646464;
}

/* For Mozilla Firefox */
::-moz-scrollbar-thumb {
  background-color: #2f2f2f;
  border-radius: 6px;
}

::-moz-scrollbar-thumb:hover {
  background-color: #646464;
}

/* For Internet Explorer */
::-ms-scrollbar-thumb {
  background-color: #2f2f2f;
  border-radius: 6px;
}

::-ms-scrollbar-thumb:hover {
  background-color: #646464;
}

/* For Webkit/Blink browsers */
::-webkit-scrollbar-track {
  background-color: #18191A;
  border-radius: 6px;
}

/* For Mozilla Firefox */
::-moz-scrollbar-track {
  background-color: #18191A;
}

/* For Internet Explorer */
::-ms-scrollbar-track {
  background-color: #18191A;
}

/* Customizing the success toast */

.Toastify__toast-icon {
  visibility: hidden;
}

.Toastify__toast--success {
  background-color: #179647EE;
  color: #fff;
  font-family: 'Poppins';
}

.Toastify__progress-bar--success {
  background-color: #5cb6ffEE;
}

.Toastify__close-button--light {
  color: white !important;
}

/* Customizing the error toast */
.Toastify__toast--error {
  background-color: #ca5555ee;
  color: #fff;
  font-family: 'Poppins';
}

.Toastify__progress-bar--error {
  background-color: #2196f3;
}

/* Customizing the info toast */
.Toastify__toast--info {
  background-color: #2196f3;
  color: white;
  font-family: 'Poppins';
}

/* Customizing the warning toast */
.Toastify__toast--warning {
  background-color: #ff9800;
}

/* Customizing the toast body */
.Toastify__toast-body {
  font-size: 16px;
}

/* Customizing the progress bar */
.Toastify__progress-bar {
  background-color: #252525;
  height: 1px;
}

.Toastify__progress-bar--wrp {
  height: 1px;
  /* visibility: hidden; */
}